<template>
  <div>
    <Tooltip
      :text='getTooltipInfos.text'
      :count='getStepId'
      :labelButton='getLabelButton'
      :linkName="getLinkName"
      :linkParams="getLinkParams"
      :linkTitle="getLinkTitle"
      :extraClass="getTooltipInfos.extraClass"
    />
    <OverlayHole />
  </div>
</template>

<script>
  import Tooltip from '@/components/elements/Tooltip.vue'
  import OverlayHole from '@/components/onBoard/OverlayHole.vue'

  export default {
    data () {
      return {
        step: [
          {
            text: 'Gr√¢ce √† ces deux frises, vous pouvez s√©lectionner un jour et une tranche horaire, et ainsi influencer l‚Äôaffichage de la carte de chaleur.',
            extraClass: 'tooltip--slider'
          },
          {
            text: 'Ces points sont des lieux o√π un √©v√®nement des J.O. aura lieu. En cliquant dessus, vous pourrez affichez les d√©tails pr√©visionnels li√©s √† celui-ci. La carte de chaleur color√©e que vous pouvez voir en fond sera influenc√©e par l‚Äô√©v√®nement s√©lectionn√©.',
            extraClass: 'tooltip--map'
          },
          {
            text: 'Ce volet affiche les d√©tails de l‚Äô√©v√®nement choisi.',
            extraClass: 'tooltip--aside tooltip--right'
          },
          {
            text: 'Ce tirroir vous affiche tous les √©v√®nements qui ont lieu pendant le jour et la tranche horaire que vous aurez choisie. En cliquant sur l‚Äôun d‚Äôeux, vous afficherez le d√©tail de cet √©v√®nements dans le volet lat√©ral droit.',
            extraClass: 'tooltip--list'
          },
          {
            text: 'Enfin, ce bouton vous permet de visualiser l‚Äôanalyse compl√®te de l‚Äô√©v√®nement choisi. Nous vous conseillons de cr√©er un compte afin de pouvoir acc√©der √† cette analyse et profiter plainement de ce que Fluidmap a √† vous offrir. √Ä vous de jouer !',
            extraClass: 'tooltip--sliderRight tooltip--rightBottom'
          }
        ]
      }
    },
    computed: {
      getStepId () {
        return parseFloat(this.$route.params.id)
      },
      getTooltipInfos () {
        return this.step[this.getStepId - 1]
      },
      getLabelButton () {
        return this.getStepId === 5 ? `C'est parti !` : 'Suivant'
      },
      getLinkName () {
        return this.getStepId === 5 ? 'Home' : 'OnBoard'
      },
      getLinkParams () {
        const nextStepId = this.getStepId === 5 ? '' : this.getStepId + 1

        return {id: nextStepId}
      },
      getLinkTitle () {
        return `Lien vers l'√©tape ${this.getStepId} du on board`
      }
    },
    created () {
      const stepId = this.getStepId
      // rajouter le cookie, si il a d√©j√† vu le onboard ou pas.
      if (stepId <= 0 || stepId > 5) {
        this.$router.push({name: 'Home'})
      }
    },
    components: {
      Tooltip,
      OverlayHole
    }
  }
</script>

<style lang="scss" scoped>

</style>



